
.matchWrap{font-size: 0.3rem;background: #F4F4F4;padding-bottom: 1rem;
}
.matchWrap .left{width: 20%;float: left;padding-top: 0.6rem;color: #7F7F7F;
}
.matchWrap .left span{display: block;
}
.matchWrap .right .team{padding:0.15rem 0;color: #333;
}
.matchWrap .right .conts{display: -webkit-box;display: -ms-flexbox;display: flex;-ms-flex-wrap: wrap;flex-wrap: wrap;color: #333;
}
.matchWrap .handicap{width: 5%;
}
.matchWrap .handicap span{display: block;height: 0.80rem;line-height: 0.80rem;
}
.matchWrap .handicap .top{background: #C2E5FF
}
.matchWrap .handicap .bot{background: #DDF4A2
}
.matchWrap .odds{width: 82%;
}
.matchWrap .odds ul{display: -webkit-box;display: -ms-flexbox;display: flex;-ms-flex-wrap: wrap;flex-wrap: wrap;background: #fff;
}
.matchWrap .odds li{width: 33%;height:0.8rem;line-height: 0.8rem;border-right: 1px solid #F4F4F4;
}
.matchWrap .odds li:nth-child(1){border-bottom: 1px solid #F4F4F4;
}
.matchWrap .odds li:nth-child(2){border-bottom: 1px solid #F4F4F4;
}
.matchWrap .odds li:nth-child(3){border-bottom: 1px solid #F4F4F4;
}
.matchWrap .expand{width: 13%;background: #fff;padding-top: 0.4rem;
}
.red{background: #E04241;color: #fff;
}



